import axios from 'axios'

// 创建axios实例
const api = axios.create({
  baseURL: '/api', // 使用代理路径，在生产环境中可以配置为实际的后端地址
  timeout: 30000, // 0.5分钟超时，适合文件上传
  headers: {
    'Content-Type': 'application/json',
  },
})

// 请求拦截器
api.interceptors.request.use(
  (config) => {
    // 在发送请求之前做些什么
    console.log('发送请求:', config.method?.toUpperCase(), config.url)
    return config
  },
  (error) => {
    // 对请求错误做些什么
    console.error('请求错误:', error)
    return Promise.reject(error)
  }
)

// 响应拦截器
api.interceptors.response.use(
  (response) => {
    // 对响应数据做点什么
    console.log('响应成功:', response.status, response.config.url)
    return response
  },
  (error) => {
    // 对响应错误做点什么
    console.error('响应错误:', error.response?.status, error.message)
    
    // 统一错误处理
    if (error.response) {
      // 服务器响应了错误状态码
      const { status, data } = error.response
      let message = '请求失败'
      
      if (status === 400) {
        message = data.message || '请求参数错误'
      } else if (status === 401) {
        message = '未授权访问'
      } else if (status === 403) {
        message = '禁止访问'
      } else if (status === 404) {
        message = '请求的资源不存在'
      } else if (status === 500) {
        message = '服务器内部错误'
      } else if (status >= 500) {
        message = '服务器错误'
      }
      
      error.message = message
    } else if (error.request) {
      // 请求已经发出，但没有收到响应
      error.message = '网络连接失败，请检查网络'
    } else {
      // 其他错误
      error.message = error.message || '未知错误'
    }
    
    return Promise.reject(error)
  }
)

// 文件上传专用方法
export const uploadFolder = (formData, onProgress) => {
  return api.post('/upload-folder/', formData, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
    onUploadProgress: (progressEvent) => {
      if (onProgress && progressEvent.total) {
        const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total)
        onProgress(percentCompleted)
      }
    },
  })
}

// 获取上传状态
export const getUploadStatus = (folderId) => {
  return api.get(`/upload-status/${folderId}/`)
}

// 启动分析流程
export const startAnalysis = (folderId) => {
  return api.post('/start-analysis/', {
    folder_id: folderId
  })
}

// 获取分析结果
export const getAnalysisResult = (folderId) => {
  return api.get(`/analysis-result/${folderId}/`)
}

export default api
