import { createStore } from 'vuex'

export default createStore({
  state: {
    backendStatus: 'unknown'
  },
  mutations: {
    setBackendStatus(state, status) {
      state.backendStatus = status
    }
  },
  actions: {
    async checkBackendStatus({ commit }) {
      console.log('Starting backend status check...')
      
      // Retry mechanism
      for (let attempt = 1; attempt <= 3; attempt++) {
        try {
          console.log(`Attempt ${attempt}: Fetching from http://localhost:8021/api/health/`)
          const response = await fetch('http://localhost:8021/api/health/', {
            method: 'GET',
            headers: {
              'Content-Type': 'application/json',
            },
            mode: 'cors'
          })
          
          console.log('Response received:', response.status, response.statusText)
          console.log('Response headers:', [...response.headers.entries()])
          
          if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`)
          }
          
          const data = await response.json()
          console.log('Backend response data:', data)
          commit('setBackendStatus', data.status)
          console.log('Status updated to:', data.status)
          return // Success, exit retry loop
          
        } catch (error) {
          console.error(`Attempt ${attempt} failed:`, error)
          if (attempt === 3) {
            // Final attempt failed
            commit('setBackendStatus', 'error')
          } else {
            // Wait before retry
            await new Promise(resolve => setTimeout(resolve, 1000))
          }
        }
      }
    }
  },
  modules: {
  }
})
